@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
.recipe-list {
  border: 1px solid #000;
  min-height: 100vh;
  width: 50%;
}

.recipe-list__add-recipe-btn-container {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.recipe {
  padding: 15px;
  padding-bottom: 40px;
  border-bottom: 1px solid #000;
}

.recipe__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.recipe__title {
  margin: 0;
  font-size: 1.25rem;
}

.recipe__title:after {
  content: "";
  display: block;
  margin-top: 7px;
  width: 60%;
  border-bottom: 2px solid #333;
}

.recipe:last-child {
  border-bottom: none;
  padding-bottom: 10px;
}

.recipe__row {
  margin-top: 10px;
}

.recipe__label {
  margin-right: 5px;
  font-weight: 700;
  font-size: 1rem;
}
.recipe__value {
  font-weight: lighter;
}

.recipe__value--indented {
  margin-top: 10px;
  margin-left: 20px;
}
.recipe__value--instructions {
  white-space: pre-wrap;
}

.ingredient-grid {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-row-gap: 5px;
  row-gap: 5px;
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
          column-gap: 40px;
  justify-content: flex-start;
}

.btn {
  background: none;
  border: 0;
  outline: none;
  font-weight: bold;
  font-size: inherit;
  border-radius: 5px;
  padding: 5px 20px;
  cursor: pointer;
}

.btn--primary {
  color: #fff;
  background-color: hsl(200, 100%, 50%);
}

.btn--primary:hover {
  background-color: hsl(200, 100%, 35%);
}

.btn--danger {
  color: #fff;
  background-color: hsl(0, 100%, 50%);
}

.btn--danger:hover {
  background-color: hsl(0, 100%, 35%);
}

.recipe-edit {
  padding: 30px;
  padding-top: 10px;
  position: fixed;
  right: 0;
  top: 0;
  width: 50%;
  max-height: 100%;
  overflow-y: auto;
}

.recipe-edit__remove-button-container {
  text-align: end;
}

.recipe-edit__remove-button {
  padding: 0;
  background: 0;
  outline: 0;
  border: 0;
  font-size: 1.5rem;
}

.recipe-edit__details-grid {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-row-gap: 10px;
  row-gap: 10px;
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
          column-gap: 40px;
}

.recipe-edit__label {
  font-weight: bold;
}

.recipe-edit__input {
  border: 1px solid #000;
  border-radius: 5px;
  font-size: inherit;
  padding: 5px 10px;
  outline: 0;
  width: 100%;
}

textarea.recipe-edit__input {
  resize: none;
  height: 200px;
}

.recipe-edit_ingredient-grid {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  gap: 10px;
  margin-top: 20px;
  margin-left: 40px;
}

.recipe-edit__add-ingredient-btn-container {
  margin-top: 20px;
  text-align: center;
}

@media (max-width: 1000px) {
  .recipe-edit__details-grid,
  .recipe-edit_ingredient-grid {
    grid-template-columns: 1fr;
  }
}

